import React, { Component } from 'react';
import { connect } from "react-redux";
import { baseUrl } from '../../Assets/AssetsHelper';
import SVG from 'react-inlinesvg';
import { resetFilter } from '../../Services/Actions/assetsActions'
import { uploadModalClick } from '../../Services/Actions/homeActions';
import Swal from 'sweetalert2';
import { manageAsideMenuPermissions } from '../../Services/Utils/utils';
import { menuData, helpData } from '../../Services/Utils/config';

class AsideMenu extends Component {
    constructor() {
        super();
        this.state = {
            support: false
        };
    }
    submenuDropdown = (val) => {
        if (val === 'collections') this.setState({ support: false })
        else this.setState({ support: !this.state.support })
    }
    closeAsideToggle = (val, assetsStatus,home) => {
        if (val === 'main' && home){
            this.setState({support: false})
            this.props.history('/home')
        }
        else if (val === 'child-menu') this.setState({support: false})
        var rootEle = document.getElementById('ugc-suit-main');
        if (rootEle) rootEle.classList.remove('aside-on');
        if (assetsStatus && String(window.location.href).includes("assets")) {
            const overlay = document.querySelector(".offcanvas-backdrop")
            if (overlay) overlay.click()
        }
        if(home) this.props.history("/home")
    }
    menuActiveClass = (menuLink) => window.location.href && String(window.location.href).includes(menuLink) ? 'active' : '';
    managePermission = (checkPermission) => {
        const { userData, history } = this.props;
        const permissions = new Set(['manage-connection', 'assets', 'template', 'analytics', 'trash']);
        if (userData.collaborator != '' && userData.collaborator.permissions != "" && userData.collaborator.activeAccount != 144127) {
            const isBlocked = manageAsideMenuPermissions(checkPermission, userData);
            if (isBlocked) this.permissionError(); else {
                if (checkPermission == 'manage-connection') {
                    this.closeAsideToggle('main');
                    userData.collaborator.permissions.networkManager.manageConnection ? history("/manage-connection") : history("/social-account");
                } else if (checkPermission == 'assets') {
                    this.closeAsideToggle('main', true);
                    history("/assets");
                } else history(`/${checkPermission}`);
            }
        } else if (userData.user.id == 144127 || (userData.collaborator != '' && userData.collaborator.activeAccount == 144127)) {
            if (permissions.has(checkPermission)) {
                this.closeAsideToggle('main', checkPermission == 'assets' ? true : null);
                history(`/${checkPermission}`);
            } else if (checkPermission == 'rule' || checkPermission == 'creators') this.customUserError();
        } else {
            this.closeAsideToggle('main', checkPermission == 'assets' ? true : null);
            history(`/${checkPermission}`);
        }
    }
    showError = (message) => Swal.fire({
        title: 'Oops!',
        text: message,
        icon: 'error',
    })

    permissionError = () => this.showError("It seems you're not currently authorized to use this module. Please reach out to your Administrator to request access.");
    customUserError = () => this.showError("You do not have access to this feature. Please contact your account manager to get access.");
    render() {
        const { support } = this.state;
        const { menuActiveClass } = this
        return <div className="sidebar-menu" onClick={event => this.props.uploadModalClick(false)}>
            <ul className="sidebar-menu-list list-unstyled mb-0 p-0">
                {menuData.map(item=>{
                    return <>
                        <li className="sidebar-menu-items">
                            <div className={`nav-menu-item block-center flex-lg-column text-decoration-none ${item.class == 'manage-connection' ? menuActiveClass('social-account') : ''} ${menuActiveClass(item.class)}`} activeclassname="active" onClick={() => {item.method == 'main' ? this.closeAsideToggle(item.method,null,true) : this.managePermission(item.method)}} >
                                <span className="menu-icon me-2 me-lg-0 mb-0 mb-lg-2">
                                    {item.icon == 'connection' ? <SVG src={baseUrl('media/icons/svg-icons/connection.svg')} /> :
                                    <i className={item.icon}></i>}
                                </span>
                                <span className="menu-text">{item.name}</span>
                            </div>
                        </li>
                        {item.name == "Creators" ? <hr className="w-75 d-lg-flex d-none" style={{ margin: '2vh auto' }} /> : null}
                    </>
                })}
            </ul>
            <ul className="sidebar-menu-list list-unstyled mb-0 p-0">
                <li className={`sidebar-menu-items ${support ? 'active' : ''}`}>
                    <span className="menu-toggle nav-menu-item block-center flex-lg-column text-decoration-none justify-content-between" onClick={() => this.submenuDropdown('support')}>
                        <span className="menu-icon me-2 me-lg-0 mb-0 mb-lg-2"><i className="ri-question-line"></i></span>
                        <span className="menu-text flex-grow-1">Help Center</span>
                        <span className="menu-arrow"></span>
                    </span>
                    <div className="menu-submenu">
                        <ul className="submenu-nav list-unstyled">
                            {helpData.map(item => {
                                return <li className="submenu-nav-item">
                                    <a onClick={item.clickMethod} className="nav-menu-item block-center text-decoration-none">
                                        <span className="menu-icon me-2"><i className={item.icon}></i></span>
                                        <span className="menu-text">{item.name}</span>
                                    </a>
                                </li>
                            })}
                        </ul>
                    </div>
                </li>
                <li className="sidebar-menu-items">
                    <div className={`nav-menu-item block-center flex-lg-column text-decoration-none ${menuActiveClass("trash")}`} activeclassname="active" onClick={() => this.managePermission('trash')}>
                        <span className="menu-icon me-2 me-lg-0 mb-0 mb-lg-2"><i className="ri-delete-bin-line"></i></span>
                        <span className="menu-text">Trash</span>
                    </div>
                </li>
            </ul>
        </div>
    }
}

const mapStateToProps = (state) => {
    return {
        userData: state.authenticated.userData,
    }
}

const mapDispatchToProps = dispatch => {
    return {
        resetFilter: () => dispatch(resetFilter()),
        uploadModalClick: (status) => dispatch(uploadModalClick(status))
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(AsideMenu);